import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { AppBar, Divider, SwipeableDrawer } from "@material-ui/core";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import FlareIcon from "@material-ui/icons/Flare";
import { Box, ThemeProvider, useScrollTrigger } from "@material-ui/core";

import logo from "../images/twitter-logo.png";

import { createMuiTheme } from "@material-ui/core/styles";

import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";

import InboxIcon from "@material-ui/icons/MoveToInbox";
import MailIcon from "@material-ui/icons/Mail";
import Slide from "@material-ui/core/Slide";

const theme = createMuiTheme({
  shadows: ["none"],
  palette: {
    primary: {
      light: "#757ce8",
      main: "#1DA1F2",
      dark: "#002884",
      contrastText: "#fff",
    },
    secondary: {
      light: "#ff7961",
      main: "#14171A",
      dark: "#ba000d",
      contrastText: "#000",
    },
  },
});

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  twLogo: {
    height: 22,
  },
  list: {
    width: 270,
  },
  fullList: {
    width: "auto",
  },
  nav: {
    borderBottom: "1px solid #b5d1e4",
    padding: theme.spacing(0.5, 0, 0.5),
  },
}));

function HideOnScroll(props) {
  const { children } = props;

  const trigger = useScrollTrigger();

  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}

function Header(props) {
  const classes = useStyles();

  const [state, setState] = React.useState({
    drawerOpen: false,
  });

  const toggleDrawer = (open) => (event) => {
    if (
      event &&
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setState({ ...state, drawerOpen: open });
  };

  const list = () => {
    return (
      <div
        className={classes.list}
        role="presentation"
        onClick={toggleDrawer(false)}
        onKeyDown={toggleDrawer(false)}
      >
        <List>
          {["Inbox", "Starred", "Send email", "Drafts"].map((text, index) => (
            <ListItem button key={text}>
              <ListItemIcon>
                {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
              </ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          ))}
        </List>
        <Divider />
      </div>
    );
  };

  return (
    <ThemeProvider theme={theme}>
      <div className={classes.root}>
        <HideOnScroll {...props}>
          <AppBar
            position="fixed"
            color="inherit"
            elevation={0}
            className={classes.nav}
          >
            <Toolbar style={{ justifyContent: "space-between" }}>
              <React.Fragment key={state.drawerOpen}>
                <IconButton
                  edge="start"
                  className={classes.menuButton}
                  color="inherit"
                  aria-label="menu"
                  onClick={toggleDrawer(true)}
                >
                  <MenuIcon color="primary" className={classes.twLogo} />
                </IconButton>

                <SwipeableDrawer
                  anchor="left"
                  open={state.drawerOpen}
                  onClose={toggleDrawer(false)}
                  onOpen={toggleDrawer(true)}
                  elevation={0}
                >
                  {list()}
                </SwipeableDrawer>
              </React.Fragment>

              <img src={logo} alt="twitter" className={classes.twLogo} />

              <Box>
                <IconButton color="inherit">
                  <FlareIcon color="primary" />
                </IconButton>
              </Box>
            </Toolbar>
          </AppBar>
        </HideOnScroll>
      </div>
    </ThemeProvider>
  );
}

export default Header;
